import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/layout"
import { pageInCulture } from "../selectors"
import Header from "../components/header"
import PageIntroduction from "../components/pageIntroduction"

const ProductCarouselSlide = ({
  productDetailLinkText,
  productDetailType,
  variantsLabel,
  applicationLabel,
  type,
  variants,
  application,
  thumbnail,
  url,
  productTitle,
}) => {
  const { mobile } = thumbnail
  return (
    <div className="carousel-inner">
      <div className="carousel-item active">
        <div className="product">
          <div className="header">
            <img src={mobile} className="d-block m-auto" srcSet={mobile} alt={""} />
          </div>
          <h2 className="title">{productTitle}</h2>
          <div className="content">
            <h3>{productDetailType}</h3>
            <p>{type}</p>
            <h3>{variantsLabel}</h3>
            <p>{variants}</p>
            <h3>{applicationLabel}</h3>
            <p>{application}</p>
            <div className="corner" />
          </div>
          <Link to={url}>
            {productDetailLinkText}
            <span className="corner" />
            <span className="inner-corner" />
          </Link>
        </div>
      </div>
    </div>
  )
}

const Product = ({
  productTitle,
  productDetailLinkText,
  productDetailType,
  variantsLabel,
  applicationLabel,
  type,
  variants,
  application,
  thumbnail,
  url,
}) => {
  const { desktop, large_desktop } = thumbnail
  return (
    <div className="product">
      <div className="header">
        <picture>
          <source media="(min-width: 75em)" srcSet={large_desktop} />
          <img src={desktop} className="d-block m-auto" srcSet={desktop} alt={""} />
        </picture>
      </div>
      <h2 className="title">{productTitle}</h2>
      <div className="content">
        <h3>{productDetailType}</h3>
        <p>{type}</p>
        <h3>{variantsLabel}</h3>
        <p>{variants}</p>
        <h3>{application}</h3>
        <p>{applicationLabel}</p>
        <div className="corner" />
      </div>
      <Link to={url}>
        {productDetailLinkText}
        <span className="corner" />
        <span className="inner-corner" />
      </Link>
    </div>
  )
}

export default ({ data, pageContext }) => {
  const currentCulture = pageContext.culture
  const products = pageInCulture(currentCulture, data.products)
  const productItems = data.allProduct.edges.map(({ node }) => {
    return pageInCulture(currentCulture, node)
  })

  return (
    <Layout culture={pageContext.culture} currentPageId={pageContext.umbracoId}>
      <Header currentPage={products} />
      <PageIntroduction {...products} />
      <section>
        <div className="container">
          <div id="products">
            <div
              id="products-carousel"
              class="carousel slide d-md-none"
              data-ride="carousel"
            >
              {productItems.map(pi => (
                <ProductCarouselSlide {...products} {...pi} />
              ))}
            </div>
            <div id="products-grid">
              {productItems.map(pi => (
                <Product {...products} {...pi} />
              ))}
            </div>
          </div>
        </div>
        <div className="edge-container w-100">
          <div className="edge"></div>
        </div>
      </section>

    </Layout>
  )
}
export const query = graphql`
  query {
    products {
      url {
        nl
        en
      }
      id
      headerImage {
        nl {
          mobile
          tablet
          desktop
          large_desktop
        }
        en {
          mobile
          tablet
          desktop
          large_desktop
        }
      }
      headerImageAlt {
        en
        nl
      }
      aboveTitle {
        nl
        en
      }
      title {
        nl
        en
      }
      lead {
        nl
        en
      }
      productDetailType {
        nl
        en
      }
      productDetailLinkText {
        nl
        en
      }
      variantsLabel: variants {
        nl
        en
      }
      applicationLabel: application {
        nl
        en
      }
    }
    allProduct {
      edges {
        node {
          key: umbracoId
          url {
            en
            nl
          }
          type {
            nl
            en
          }
          application {
            en
            nl
          }
          variants {
            en
            nl
          }
          lead {
            nl
            en
          }
          productTitle: aboveTitle {
            en
            nl
          }
          thumbnail {
            nl {
              mobile
              desktop
              large_desktop
            }
            en {
              mobile
              desktop
              large_desktop
            }
          }
        }
      }
    }
  }
`
